import React from 'react';
import { graphql, Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../../components/layout';

export default function News(props) {
  return (
    <Layout
      page='news'
      subtitle='新着情報'
      subtitleEn='NEWS'
      breadcrumbs={[{ link: '/news/', label: '新着情報' }]}
    >
      <main className='l-main'>
        <div className='c-news c-news--row'>
          {props.data.allMarkdownRemark.edges.map(
            (
              {
                node: {
                  excerpt,
                  frontmatter: { slug, date, title }
                }
              },
              i
            ) => (
              <article key={i} className='c-news__item'>
                <Link to={slug} className='c-news__img'>
                  <StaticImage
                    src='../../images/thumbnail-300x300.jpg'
                    alt={title}
                  />
                </Link>
                <div className='c-news__detail'>
                  <div className='c-news__date'>
                    <time dateTime={date}>{date}</time>
                  </div>
                  <ul className='c-list c-list--tag'>
                    <li>
                      <span className='c-tag' style={{ color: '#fff' }}>
                        お知らせ
                      </span>
                    </li>
                  </ul>
                  <h1 className='c-news__title'>
                    <Link to={slug}>{title}</Link>
                  </h1>
                  <p className='c-news__aside'>
                    {excerpt}{' '}
                    <Link to={slug} className='c-link c-link--inline'>
                      続きを読む
                    </Link>
                  </p>
                </div>
              </article>
            )
          )}
        </div>
      </main>
    </Layout>
  );
}

export const pageQuery = graphql`
  {
    allMarkdownRemark(
      filter: { frontmatter: { hidden: { ne: 1 } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      edges {
        node {
          excerpt(pruneLength: 140)
          frontmatter {
            slug
            date(formatString: "YYYY/MM/DD")
            title
          }
        }
      }
    }
  }
`;
